/* App.css */
@import url("https://fonts.googleapis.com/css?family=Raleway");

.App {
  text-align: center;
}

.App-logo {
  height: 20vmin; /* Adjusted height for smaller screens */
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: auto; /* Remove fixed height */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Additional styling for HomePage component */

.container {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 10px; /* Adjusted padding for smaller screens */

}

.heading {
  font-size: 24px;
  margin-bottom: 20px;
  color: white;
}

.uploadContainer, .compareButton, .resultsContainer {
  width: 100%; /* Make elements full width */
  margin: 10px 0; /* Adjust margin */
}

.fileInput {
  background-color: black;
  color: white;
  border: 2px solid white;
  border-radius: 5px;
  padding: 5px;
}

.compareButton {
  margin-top: 20px;
}

.resultsContainer {
  margin-top: 20px;
}

.resultText {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

.resultList {
  list-style: none;
  padding: 0;
}

.resultListItem {
  font-size: 16px;
  margin-bottom: 5px;
}

a {
  color: var(--glow-color);
  /* Other styles for links */
}

.link-class {
  color: var(--glow-color);
  /* Other styles for links with this class */
}

:root {
  --glow-color: hsl(186 100% 69%);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  overflow-x: auto; /* Allow horizontal scrolling */
}

body {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: black;
}

.glowing-header {
  color: rgba(0, 0, 0, 0.8);
  text-shadow: none;
  animation: none;

  position: relative;
    cursor: pointer;
    padding: 0.35em 1em;
    border: 0.15em solid var(--glow-color);
    border-radius: 0.45em;
    background: var(--glow-color);
    /* perspective: 2em; */
    font-family: "Raleway", sans-serif;
    font-size: 1em;
    font-weight: 900;
    box-shadow: inset 0px 0px 1em 0px var(--glow-color), 0px 0px 1em 0px var(--glow-color);
    animation: border-flicker 2s linear infinite;
}

.glowing-btn {
  position: relative;
  color: var(--glow-color);
  cursor: pointer;
  padding: 0.35em 1em;
  border: 0.15em solid var(--glow-color);
  border-radius: 0.45em;
  background: none;
  /* perspective: 2em; */
  font-family: "Raleway", sans-serif;
  font-size: 1em;
  font-weight: 900;

  -webkit-box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  -moz-box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  animation: border-flicker 2s linear infinite;
}

.glowing-txt {
  float: left;
  margin-right: -0.8em;
  -webkit-text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3),
    0 0 0.45em var(--glow-color);
  -moz-text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3),
    0 0 0.45em var(--glow-color);
  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em var(--glow-color);
  animation: text-flicker 3s linear infinite;
}

.faulty-letter {
  opacity: 0.5;
  animation: faulty-flicker 2s linear infinite;
}

.glowing-btn::before {
  content: "";
  /* position: absolute; perspective related */
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.7;
  filter: blur(1em);
  transform: translateY(120%) rotateX(95deg) scale(1, 0.35);
  background: var(--glow-color);
  pointer-events: none;
}

.glowing-btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  z-index: -1;
  background-color: var(--glow-color);
  box-shadow: 0 0 2em 0.2em var(--glow-color);
  transition: opacity 100ms linear;
}

.glowing-btn:hover {
  color: rgba(0, 0, 0, 0.8);
  text-shadow: none;
  animation: none;
}

.glowing-btn:hover .glowing-txt {
  animation: none;
}

.glowing-btn:hover .faulty-letter {
  animation: none;
  text-shadow: none;
  opacity: 1;
}

.glowing-btn:hover:before {
  filter: blur(1.5em);
  opacity: 1;
}

.glowing-btn:hover:after {
  opacity: 1;
}

input[type=file]::file-selector-button {
  border: 2px solid var(--glow-color);
  padding: .2em .4em;
  border-radius: .2em;
  background-color: var(--glow-color);
  transition: 1s;
  font-family: "Raleway", sans-serif;
  font-size: 1em;
  font-weight: 900;
}

input[type=file]::file-selector-button:hover {
  border: 2px solid black;
  padding: .2em .4em;
  border-radius: .2em;
  background-color: black;
  transition: 1s;
  font-family: "Raleway", sans-serif;
  font-size: 1em;
  font-weight: 900;  
  color:var(--glow-color);
}

@keyframes faulty-flicker {
  0% {
    opacity: 0.1;
  }
  2% {
    opacity: 0.1;
  }
  4% {
    opacity: 0.5;
  }
  19% {
    opacity: 0.5;
  }
  21% {
    opacity: 0.1;
  }
  23% {
    opacity: 1;
  }
  80% {
    opacity: 0.5;
  }
  83% {
    opacity: 0.4;
  }

  87% {
    opacity: 1;
  }
}

@keyframes text-flicker {
  0% {
    opacity: 0.1;
  }

  2% {
    opacity: 1;
  }

  8% {
    opacity: 0.1;
  }

  9% {
    opacity: 1;
  }

  12% {
    opacity: 0.1;
  }
  20% {
    opacity: 1;
  }
  25% {
    opacity: 0.3;
  }
  30% {
    opacity: 1;
  }

  70% {
    opacity: 0.7;
  }
  72% {
    opacity: 0.2;
  }

  77% {
    opacity: 0.9;
  }
  100% {
    opacity: 0.9;
  }
}

@keyframes border-flicker {
  0% {
    opacity: 0.1;
  }
  2% {
    opacity: 1;
  }
  4% {
    opacity: 0.1;
  }

  8% {
    opacity: 1;
  }
  70% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 600px) {
  .glowing-btn {
    font-size: 1em;
  }
}
